.Home{
    background-image: url(/static/media/photo.9db5a6ac.jpeg);
    background-repeat: no-repeat;
    background-attachment: fixed;

}
.HeaderConteiner{
    height: 300px;
    width: 100%;
    background: white;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    display: flex;
    justify-content: center;
    align-items: center
}
.Video{
    height: 300px;
    width: 100%;
}

@media screen and (min-width:390px) and (max-width:450px){
    .HeaderConteiner{
        height: 250px;
    }
    .Video{
        height: 250px;
    }
}
.ContentConteiner{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 350px;
    width: 100%;
    margin-top: 1000px;
    background-color: rgba(255, 0, 0, 0);
    position: -webkit-sticky;
    position: sticky;
    top: 300px;
}

.Photo{
    object-fit: cover;
    height:300px;
}

.CongratulationsText{
    font-family: Pacifico;
    font-size: large;
    width: 350px;
    margin-left: 200px;
    margin-right: 200px;
}
@font-face {
    font-family: "Pacifico";
    src: url(/static/media/Pacifico.85bb2d0e.ttf) format("truetype");
    font-style: normal;
    font-weight: normal;
}

@media screen and (min-width:390px) and (max-width:450px){
    .ContentConteiner{
        height: 250px;
        top: 250px;
    }
    .Photo{
        object-fit: cover;
        height:auto;
        width: 100px;
    }
    .CongratulationsText{
        font-size: 10px;
        width: 190px;
        margin-left: 10px;
        margin-right: 10px;
    }
}

.FooterConteiner{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 350px;
    width: 100%;
    background: #000000;
    margin-top: 1000px;
    position: -webkit-sticky;
    position: sticky;
}

.CongratulationsTextIT{
    font-family: Pacifico;
    font-size: large;
    width: 300px;
    color: white;

}

.Picture{
    height: 300px;
    margin-left: 100px;
    margin-right: 100px;
}

@font-face {
    font-family: "Pacifico";
    src: url(/static/media/Pacifico.85bb2d0e.ttf) format("truetype");
    font-style: normal;
    font-weight: normal;
}
@media screen and (min-width:390px) and (max-width:450px){
    .FooterConteiner{
        height: 250px;
        margin-bottom: 150px;
    }
    .CongratulationsTextIT{
        font-size: 10px;
        width: 100px;
    }
    .Picture{
        height: auto;
        width: 100px;
        margin-left: 20px;
        margin-right: 20px;
    }
}
